
@charset "UTF-8";
* {
  font-family: 'PingFang SC','Helvetica Neue','Helvetica','Arial',sans-serif;
}
h3 {
  /*大标题*/
  padding-bottom: 15px;
  border-bottom: 1px solid #b7af8a;
  font-size: 18px;
  font-weight: 700;
  color: #565651;
  -webkit-transition: all .6s ease-out;
  transition: all .6s ease-out;
  cursor: Default;
}
h3:hover {
    text-indent: 50px;
    color: #f60;
}
h4 {
  /*加粗小标题*/
  font-size: 14px;
  font-weight: 600;
  padding: 15px 0;
}
h5 {
  /*正文*/
  line-height: 25px;
  font-size: 14px;
  font-weight: 400;
}
code {
  border: 1px solid #b7af8a;
  padding: 10px;
  display: inline-block;
  max-width: 674px;
  margin: 10px 0;
  overflow: hidden;
  background: #f5f5f5;
}
code img {
    max-width: 674px;
}
.wrap {
  padding: 15px;
}



