
.nav-wrapper {
  width: 205px;
  float: left;
  margin-top: -34px;
}
.nav-wrapper .nav-title {
    font-size: 18px;
    color: #55626e;
    font-weight: bold;
    padding: 10px 0 3px;
    text-align: center;
    /*border-bottom: 5px solid #e8eee4;*/
    margin-bottom: 10px;
}
.nav-wrapper .tab-ul {
    overflow: hidden;
    border-radius: 3px;
    /*border: 1px dotted #eee;*/
}
.nav-wrapper .tab-ul .tab-li h2 {
      font-size: 18px;
      font-weight: 500;
      height: 41px;
      line-height: 41px;
      color: #55626e;
      background: rgba(183, 175, 138, 0.31);
      padding-left: 10px;
}
.nav-wrapper .tab-ul .tab-li .list-ul .list-li {
      overflow: hidden;
}
.nav-wrapper .tab-ul .tab-li .list-ul .item {
      color: #555;
      font-size: 14px;
      display: block;
      height: 34px;
      line-height: 34px;
      background: #e8eee4;
      padding-left: 10px;
      border-bottom: 1px dotted #d4cdad;
}
.nav-wrapper .tab-ul .tab-li .list-ul .item:hover {
        background: #e4e6e2;
}
.nav-wrapper .tab-ul .tab-li .list-ul .item .dot {
        float: right;
        margin-right: 10px;
}
.nav-wrapper .tab-ul .tab-li .list-ul .listDetail {
      background: #eff3eb;
      height: 28px;
      line-height: 28px;
      border-bottom: 1px dotted #eae6e6;
}
.nav-wrapper .tab-ul .tab-li .list-ul .listDetail:hover {
        background: #e4e6e2;
}
.nav-wrapper .tab-ul .tab-li .list-ul .listDetail .detail {
        display: block;
        font-size: 13px;
        color: #7d7676;
        padding-left: 26px;
}
.nav-wrapper .tab-ul .tab-li .list-ul .inner-ul {
      display: none;
}
.nav-wrapper .tab-ul .tab-li .list-ul .inner-ul.show {
        display: block;
}



